* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-size: 100%;

}

.timer-container {
  background-image: url('./assets/vasily-koloda-8CqDvPuo_kI-unsplash.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
}

.timer {
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 7em;
  padding-top: 2em;
  width: 100%;
  height: 100%;
}

.timer div:first-child {
  text-align: center;
}

.timer-icon {
  font-size: 12.5rem;
  color: #f8f8f8;

}

h2 {
  font-weight: 600;
  margin-bottom: 0.5em;
  color: #f8f8f8;
}

.timer div:first-child p {
  color: #f8f8f8;
}


.timer div:last-child {
  border: 1px solid #f8f8f8;
  border-radius: 3px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  margin-top: 3em;
  padding: 3em;
  text-align: center;
  color: #f8f8f8;
}

section p:first-child, .timer div:last-child span {
  font-size: 3em;
}

@media screen and (max-width: 480px) {
  .timer div:last-child {
    margin-left: 2em;
    margin-right: 2em;
    padding: 3em 2em;
  }
}

@media screen and (min-width: 150px) {
  .container {
    width: 75%;

  }
}

@media screen and (min-width: 350px) {
  .container {
    width: 75%;

  }
}